html{
    padding:0;
    margin:0;
    animation-name: open;
    animation-duration:4s;
    animation-timing-function: linear;
}

body{
  background-color: unset;
   padding:20px;
   margin:0;
}

section{
  background-color: unset;
}

#header{
    width: 100%;
    margin: auto;
    padding-top:45px;
    background-size: contain;
    height: 150px;
    background: #8823ff;
}

h1{
  font-size: 350%;
  line-height: 20px;
  text-align: center;
  color: #fff;
}

#header h4 {
  margin-top: 30px;
  text-align: center;
  color: white
}

.box:hover {
  border: 3px dotted #666;
}

#add{
  padding: 10px;
  margin: 10px 25px 10px 50px;
  width: 200px;
}

#save{
  padding: 10px;
  margin: 10px 50px 10px 25px;
  width: 200px;
}

#user-info-rankings {
  min-height: 300px;
  padding: 20px 0;
}

#user-info {
  padding: 10px;
}

img {
  padding: 0 20px;
}

.item {
  margin: 10px 0;
}

.item input {
  margin: 0 5px;
}

.item-name {
  padding: 0 10px;
}

p.user-info {
  font-weight: bold;
}
  